/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

a, table.table a {
    color: $theme-color;
    &:hover {
      color: $theme-color;
      text-decoration: underline;
    }
    &:hover:after {
      width: 100%;
    }
  }
  
  // Math
  
  .equation {
    margin-bottom: 1rem;
    text-align: center;
  }
  
  // Caption
  
  .caption {
    font-size: 0.875rem;
    margin-top: 0.75rem;
    margin-bottom: 1.5rem;
    text-align: center;
  }
  
  
  // Profile
  
  .profile {
      .address {
        font-family: monospace;
        font-size: 1.2rem;
        p {
          margin: 0;
        }
      }
  }
  
  
  // Navbar customization
  
  .navbar {
    box-shadow: none;
    border-bottom: 1px solid $grey-color-light;
    opacity: 0.95;
  }
  .navbar.navbar-light {
    // Remove link decoration
    a {
      &:hover {
        text-decoration: none;
      }
    }
    .navbar-nav .nav-item .nav-link {
      &:hover {
        color: $theme-color;
      }
    }
    .navbar-nav .nav-item.active>.nav-link {
        background-color: inherit;
        color: $theme-color;
        font-weight: bolder;
        &:hover {
          color: $theme-color;
        }
    }
    .contact-icon {
      font-size: 2rem;
      a {
        color: $grey-color-dark;
        &:hover {
          color: $theme-color;
        }
      }
    }
  }
  
  .navbar-toggler {
    .icon-bar {
      display: block;
      width: 22px;
      height: 2px;
      background-color: $grey-color-dark;
      border-radius: 1px;
      margin-bottom: 4px;
      transition: all 0.2s;
    }
    .top-bar {
      transform: rotate(45deg);
      transform-origin: 10% 10%;
    }
    .middle-bar {
      opacity: 0;
    }
    .bottom-bar {
      transform: rotate(-45deg);
      transform-origin: 10% 90%;
    }
  }
  
  .navbar-toggler.collapsed {
    .top-bar {
      transform: rotate(0);
    }
    .middle-bar {
      opacity: 1;
    }
    .bottom-bar {
      transform: rotate(0);
    }
  }
  
  
  // News
  
  .news table td {
    font-size: 1rem;
  }
  
  // Social (bottom)
  
  .social {
    text-align: center;
    .contact-icon {
      font-size: 4rem;
      a {
        color: $grey-color-dark;
        &:hover {
          color: $theme-color;
        }
      }
    }
    .contact-note {
      font-size: 0.8rem;
    }
  }
  
  
  // Footer
  
  .footer {
    text-align: center;
    a {
      font-size: 0.8rem;
      color: $grey-color-dark;  
      text-decoration: none;
    }
  }

  footer.fixed-bottom {
    color: lighten($grey-color, 25%);
    background-color: $grey-color-dark;
    font-size: 0.75rem;
    .container {
      padding-top: 9px;
      padding-bottom: 8px;
    }
    a {
      color: white;
      &:hover {
        color: lighten($theme-color, 25%);
        text-decoration: none;
      }
    }
  }
  
  footer.sticky-bottom {
    border-top: 1px solid $grey-color-light;
    padding-top: 40px;
    padding-bottom: 40px;
    font-size: 0.9rem;
  }
  
  
  // Blog
  
  .header-bar {
    border-bottom: 1px solid $grey-color-light;
    text-align: left;
    padding-top: 2rem;
    padding-bottom: 2rem;
    h3 {
      color: $grey-color-dark;
      font-size: 3rem;
    }
    padding-top: 10px;
  }

  .page-description {
    max-width: 75%
  }
  
  .post-list {
    margin: 0;
    margin-bottom: 40px;
    padding: 0;
    li {
      border-bottom: 1px solid $grey-color-light;
      list-style: none;
      padding-top: 2rem;
      padding-bottom: 2rem;
      .post-meta {
        color: $grey-color;
        font-size: 0.875rem;
        margin-bottom: 0;
      }
      p {
        margin-bottom: 0px;
      }
      a {
        color: $grey-color-dark;
        text-decoration: none;
        &:hover {
          color: $theme-color;
        }
      }
      ul {
        margin-bottom: 0px;
      }
      img {
        max-width: 50%;
        max-height: 100%
      }
      h5 {
        // display: inline;
        color: $grey-color
      }
    }
  }
  
  .pagination .page-item.active .page-link {
    background-color: $theme-color;
    &:hover {
      background-color: $theme-color;
    }
  }
  
  
  // Distill
  
  .distill {
    a:hover {
      border-bottom-color: $theme-color;
      text-decoration: none;
    }
  }
  
  
  // Projects
  
  .projects {
    .grid-item {
      width: 250px;
      margin-bottom: 10px;
      a {
        color: black;
        text-decoration: none;
        &:hover {
          color: $theme-color;
        }
      }
    }
    .card-img-top {
      // width: 100%;
      height: 7vw;
      object-fit: cover;
    }
  }

  .card {
    max-width: 32%;
  }
  
  
  // Blog posts
    .blog-img {
      max-width: 100%;
    } 
    .post-footer {
      margin-bottom: 100px;
    }

  // Publications
  
  .publications {
    margin-top: 2rem;
    h2.year {
      color: $grey-color-light;
      border-top: 1px solid $grey-color-light;
      padding-top: 1rem;
      margin-top: 2rem;
      margin-bottom: -2rem;
      text-align: right;
    }
    ol.bibliography {
      list-style: none;
      padding: 0;
      margin-top: 0;
  
      li {
        margin-bottom: 1rem;
        .abbr {
          height: 2rem;
          margin-bottom: 0.5rem;
          abbr {
            display: inline-block;
            background-color: $theme-color;
            padding-left: 1rem;
            padding-right: 1rem;
          }
        }
        span {
          display: block;
        }
        .title {
          font-weight: bolder;
        }
        .author {
          a {
            border-bottom: 1px dashed $theme-color;
            &:hover {
                border-bottom-style: solid;
                text-decoration: none;
            }
          }
          > em {
            border-bottom: 1px solid;
            font-style: normal;
          }
        }
        em.star {
          border-bottom: none;
          color: $theme-color;
          font-style: normal;
        }
        a.abstract, a.bibtex {
          cursor: pointer;
        }
        .hidden {
          font-size: 0.875rem;
          max-height: 0px;
          overflow: hidden;
          text-align: justify;
          -webkit-transition: 0.15s ease;
          -moz-transition: 0.15s ease;
          -ms-transition: 0.15s ease;
          -o-transition: 0.15s ease;
          transition: all 0.15s ease;
  
          p {
            line-height: 1.4em;
            margin: 10px;
          }
          pre {
            font-size: 1em;
            line-height: 1.4em;
            padding: 10px;
          }
        }
        .hidden.open {
          max-height: 100em;
          -webkit-transition: 0.15s ease;
          -moz-transition: 0.15s ease;
          -ms-transition: 0.15s ease;
          -o-transition: 0.15s ease;
          transition: all 0.15s ease;
        }
        span.abstract.hidden {
          border: dashed 1px white;
        }
        span.abstract.hidden.open {
          border-color: $grey-color;
        }
      }
    }
  }